import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { Link } from "gatsby"
import { Helmet } from "react-helmet"

const NotFoundPage = () => (
  <Layout>
    <SEO title="404: Not found" />
    <Helmet title={"404 Not Found"}>
      <link href="https://unpkg.com/nes.css@latest/css/nes.min.css" rel="stylesheet" type="text/css" />
    </Helmet>
    <h1>404: PAGE NOT FOUND</h1>
    <section className="message-list">
      <section style={{ display: `flex`, alignItems: `center`, flexWrap: `wrap`, marginBottom: `2rem` }}>
        <i className="nes-bcrikko"></i>
        <div className="nes-balloon from-left is-dark">
          <p>Woops! Apparently this page doesn&#39;t exist...</p>
        </div>
      </section>

      <section style={{ display: `flex`, alignItems: `center`, flexWrap: `wrap` }}>
        <div className="nes-balloon from-right is-dark">
          <p>
            You should probably{" "}
            <Link to="/" className="nes-btn">
              return home!
            </Link>
          </p>
        </div>
        <i className="nes-bcrikko"></i>
      </section>
    </section>
  </Layout>
)

export default NotFoundPage
